<template><section><template>
  <h2><a href="#progress">Progress </a></h2>
  <div class="introduce">
    <z-button :text="' '" :type="'info'" @click.native="startProgress"></z-button>
  </div>
  <div class="introduce-block">
    <z-table :ths="['','','','','']" :trs="[
            ['bgc','String','','#0080ff',''],
            ['type','String','','top','(top,bottom)']
          ]">
    </z-table>
  </div>
</template>

<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-progress</span>
<span class="hljs-attr">:bgc</span>=<span class="hljs-string">"'rgb(65, 184, 131)'"</span>
<span class="hljs-attr">:type</span>=<span class="hljs-string">"'bottom'"</span>
<span class="hljs-attr">ref</span>=<span class="hljs-string">"progress"</span>
&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-progress</span>&gt;</span>
</code></pre>
<pre><code class="language-js">methods: {
  <span class="hljs-attr">startProgress</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
    <span class="hljs-keyword">this</span>.$refs.progress.startProgress()
  }
}
</code></pre>
<p><code class="z-code">this.startProgress()</code></p>
</section></template>

<script>
export default {
  mounted() {
    this.startProgress()
  },

  methods: {
    startProgress: function() {
      this.$parent.$refs.progress.startProgress()
    }
  }
}
</script>


