    <z-aside :show="asideLeft.show" :title="''" :type="'left'" @close-aside="asideLeft.show = false">
      <h3>Aside-Left</h3>
      <div>HTML</div>
      <p>&lt;aside&gt;</p>
      <div></div>
    </z-aside>
  </div>
  <div class="introduce">
    <z-button :text="'Top'" @click.native="asideTop.show = true">
    </z-button>
    <z-aside :show="asideTop.show" :title="''" :type="'top'" @close-aside="asideTop.show = false">
      <h3>Aside-Top</h3>
      <div>HTML</div>
      <p>&lt;aside&gt;</p>
      <div></div>
    </z-aside>
  </div>
  <div class="introduce">
    <z-button :text="'Right'" @click.native="asideRight.show = true">
    </z-button>
    <z-aside :show="asideRight.show" :title="''" :type="'right'" @close-aside="asideRight.show = false">
      <h3>Aside-Right</h3>
      <div>HTML</div>
      <p>&lt;aside&gt;</p>
      <div></div>
    </z-aside>
  </div>
  <div class="introduce">
    <z-button :text="'Bottom'" @click.native="asideBottom.show = true">
    </z-button>
    <z-aside :show.sync="asideBottom.show" :title="''" :type="'bottom'" @close-aside="asideBottom.show = false">
      <h3>Aside-Bottom</h3>
      <div>HTML</div>
      <p>&lt;aside&gt;</p>
      <div></div>
    </z-aside>
  </div>
  <div class="introduce-block">
    <p>HTML</p>
    <p><code class="z-code">@close-aside="xxx.show=false"</code><code class="z-code">show</code></p>
    <z-table :ths="['','','','','']" :trs="[
            ['show','Boolean','','-',''],
            ['title','String','','-',''],
            ['type','String','','left','(left,top,right,bottom)'],
            ['close','String','','','']
          ]">
    </z-table>
  </div>
</template>

<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span>
<span class="hljs-attr">:text</span>=<span class="hljs-string">"'Left'"</span>
