  <span class="hljs-comment">&lt;!--  --&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">z-aside</span>&gt;</span>
</code></pre>
</section></template>

<script>
export default {
  data() {
    return {
      asideLeft: {
        show: false
      },
      asideTop: {
        show: false
      },
      asideRight: {
        show: false
      },
      asideBottom: {
        show: false
      }
    }
  }
}
</script>


