<template><section><template>
  <h2><a href="#modal">Modal </a></h2>
  <div class="introduce">
    <z-button :text="'Alert'" @click.native="alert.show = true">
    </z-button>
    <z-modal :show="alert.show" :content="'alert'" @close-modal="alert.show = false">
    </z-modal>
  </div>
  <div class="introduce">
    <z-button :text="'Confirm'" @click.native="confirm.show = true">
    </z-button>
    <z-modal :show="confirm.show" :type="'confirm'" :content="'confirm'" @close-modal="confirm.show = false"></z-modal>
  </div>
  <div class="introduce-block">
    <p><code class="z-code">@close-modal="xxx.show=false"</code><code class="z-code">show</code></p>
    <z-table :ths="['','','','','']" :trs="[
            ['show','Boolean','','-',''],
            ['content','String','','-',''],
            ['type','String','','alert','(alert,confirm)'],
            ['title','String','','',''],
            ['sure','String','','',''],
            ['cancle','String','','',''],
            ['sureEvent','Function','','-',''],
            ['cancelEvent','Function','','-',''],
          ]">
    </z-table>
  </div>
</template>

<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span>
<span class="hljs-attr">:text</span>=<span class="hljs-string">"'Alert'"</span>
<span class="hljs-attr">@click.native</span>=<span class="hljs-string">"alert.show = true"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">z-modal</span>
<span class="hljs-attr">:show</span>=<span class="hljs-string">"alert.show"</span>
<span class="hljs-attr">:content</span>=<span class="hljs-string">"'alert'"</span>
<span class="hljs-attr">@close-modal</span>=<span class="hljs-string">"alert.show = false"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">z-modal</span>&gt;</span>
</code></pre>
</section></template>

<script>
export default {
  data() {
    return {
      alert: {
        show: false
      },
      confirm: {
        show: false
      }
    }
  }
}
</script>


